@use "global" as *;

.c-top-btn {
  display: block;
  width: rem(45);
  height: rem(45);
  padding: rem(16.5) rem(10.5);
  text-align: center;
  text-decoration: none;
  background: $white;
}

.c-top-btn::after {
  display: inline-block;
  content: "";
  width: rem(18);
  height: rem(18);
  border-top: 2px solid $black3;
  border-left: 2px solid $black3;
  transform: rotate(45deg);
}

.c-top-btn:focus,
.c-top-btn:hover {
  background: $black3;
  transition: 0.3s;
}

.c-top-btn:focus::after,
.c-top-btn:hover::after {
  display: inline-block;
  content: "";
  width: rem(18);
  height: rem(18);
  border-top: 2px solid $white;
  border-left: 2px solid $white;
  transform: rotate(45deg);
}